const Baron = require('./react-baron');

class WeaNewScroll extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            scrollTop: 0,
        }
    }
    scrollerToTop() {
        if (this.refs.weaBaron.getScroller()) this.refs.weaBaron.getScroller().scrollTop = 0;
        if (this.refs.weaBaron.getBar()) this.refs.weaBaron.getBar().top = 0;
    }
    scrollToLast() {
        this.refs.weaBaron.scrollToLast && this.refs.weaBaron.scrollToLast();
    }
    scroll(number) {
        if (this.refs.weaBaron.getScroller()) this.refs.weaBaron.getScroller().scrollTop = number;
        if (this.refs.weaBaron.getBar()) this.refs.weaBaron.getBar().top = number;
    }
    getScrollTop() {
        let v = 0;
        if (this.refs.weaBaron.getScroller()) v = this.refs.weaBaron.getScroller().scrollTop;
        return v;
    }
    onScroll(...args) {
        const scrollTop = this.getScrollTop();
        this.setState({scrollTop});
        this.props.onScroll && this.props.onScroll(...args);
    }
    render() {
        const {toTop, toTopTitle = '滚动到顶部', height} = this.props;
        const {scrollTop} = this.state;
        const style = this.props.style || {};
        if (height) style.height = height;
        return (
            <div className={`wea-new-scroll ${this.props.className || ''}`} ref="weaNewScroll" style={style} id={this.props.scrollId}>
                <Baron $={jQuery}
                    {...this.props}
                    ref="weaBaron"
                    clipperCls="clipper"
                    scrollerCls="scroller"
                    trackCls="track"
                    barCls="bar"
                    onScroll={this.onScroll.bind(this)}
                    barOnCls="baron">
                        {this.props.children}
                </Baron>
                {
                    toTop && scrollTop > 0 && <div className="returnToTop" title={toTopTitle}
                        onClick={this.scrollerToTop.bind(this)}>
                        <span className="icon-coms-upper"/>
                    </div>
                }
            </div>
        );
    }
    update = () => {
    	this.refs.weaBaron && this.refs.weaBaron.baron.update();
    }
}

export default WeaNewScroll


