import React from "react";
import { Row, Col, Spin, message } from "antd";
import { routerShape } from "react-router";
import classnames from "classnames";

import {callApi} from "../wea-tools/callApi";
import Extents from "../wea-rich-text/Extents";
import WeaQrcode from "../wea-qrcode";
import WeaNewScroll from "../wea-new-scroll";
import WeaLocaleProvider from "../wea-locale-provider";
import { defaultIcon, createQRCode, sendMsgToPC } from "./util";
import WindowFunc from "../wea-tools/window";

const getLabel = WeaLocaleProvider.getLabel;

class WeaPopoverHrm extends React.Component {
  static sendMsgToPC = sendMsgToPC;
  static contextTypes = {
    router: routerShape
  };
  constructor(props) {
    super(props);
    this.instanceIsMounted = false;
    this.getHash = () =>
      Math.random()
        .toString(36)
        .substr(2, 6);
    this.state = {
      random: `${new Date().getTime()}_${this.getHash()}`,

      userid: "",
      userinfo: "",
      validate: {},
      visible: false,
      showSQR: false,
      x: -1000,
      y: -1000,
      loading: false,
      imgLoading: false,
      targetHeight: 0,
      targetWidth: 0,

      pWidth: 0,
      pHeight: 0,
      pTop: 0,
      pLeft: 0,

      winW: WindowFunc.getTop().document.body.clientWidth,
      winH: WindowFunc.getTop().document.body.clientHeight,
      showBigImg: false,
      bigImgWidth: 142,
      bigImgHeight: 274
    };
  }
  componentDidMount() {
    this.instanceIsMounted = true;
    this.listenRouter();

    let pointerXYOld = null;
    if (typeof window.pointerXY === "function") {
      pointerXYOld = window.pointerXY;
    }
    window.pointerXY = e => {
      this.target = e.target;
      document.addEventListener("click", this.clickHandle);
      const { random } = this.state;
      const cards = $(e.target).closest(".wea-popover-hrm-relative-parent");
      let card = { id: "" };
      if (cards.length > 0) {
        card = cards[0];
      } else {
        const bodyCards = $(
          ".wea-popover-hrm-inbody .wea-popover-hrm-relative-parent"
        );
        if (bodyCards.length > 0) {
          card = bodyCards[0];
        } else {
          card = $(".wea-popover-hrm-relative-parent")[0];
        }
      }
      let iframeOffsetX = 0;
      let iframeOffsetY = 0;
      if (!card) {
        card =
          WindowFunc.getTop().$(`#wea_popover_hrm_${random}`)[0] ||
          WindowFunc.getTop().$(".wea-popover-hrm-relative-parent")[0];
        WindowFunc.getTop().document.addEventListener(
          "click",
          this.clickHandle
        );
        let iframeOffset = $(window.frameElement).offset();
        iframeOffsetX = iframeOffset.left;
        iframeOffsetY = iframeOffset.top;
      }
      if (card.id === `wea_popover_hrm_${random}`) {
        this.getParentStyle();
        this.setState({
          x: $(e.target).offset().left + iframeOffsetX,
          y: $(e.target).offset().top + iframeOffsetY,
          targetWidth: e.target.offsetWidth,
          targetHeight: e.target.offsetHeight,
          visible: true,
          winW: WindowFunc.getTop().document.body.clientWidth,
          winH: WindowFunc.getTop().document.body.clientHeight
        });
        e.stopPropagation && e.stopPropagation();
      } else {
        pointerXYOld && pointerXYOld(e);
      }
    };

    let openhrmOld = null;
    if (typeof window.openhrm === "function") {
      openhrmOld = window.openhrm;
    }
    window.openhrm = id => {
      const change = id && this.state.userid !== id;
      if (change) {
        const initStates = {
          loading: true,
          imgLoading: true,
          userid: id,
          userinfo: "",
          showBigImg: false
        };
        this.setState(initStates);
        callApi("/api/hrm/simpleinfo/getHrmSimpleInfo", "GET", {
          userid: id
        }).then(res => {
          if (res.status === "1") {
            const userinfo = res.simpleInfo;
            const { validate } = res;
            userinfo.userimg =
              validate[36] &&
              validate[36].isValidate &&
              userinfo.resourceimageid
                ? `/weaver/weaver.file.FileDownload?fileid=${
                    userinfo.resourceimageid
                  }`
                : defaultIcon[userinfo.sex];
            const img = new Image();
            img.onload = () => this.setState({ imgLoading: false });
            img.onerror = () => this.setState({ imgLoading: false });
            img.src = userinfo.userimg;
            this.setState({ userinfo, validate, loading: false });
          } else {
            this.setState({ loading: false });
            message.error("人力卡片接口错误！");
          }
        });
      }
      openhrmOld && openhrmOld(id);
    };

    this.getParentStyle();
    $(window).resize(() => {
      this.instanceIsMounted && this.getParentStyle();
    });
    // $(document).on('click', () => {
    //   if (this.instanceIsMounted) {
    //     const { visible } = this.state;
    //     visible && this.setState({ visible: false });
    //   }
    // });
  }
  componentWillUnmount() {
    this.instanceIsMounted = false;
    document.removeEventListener("click", this.clickHandle);
    WindowFunc.getTop().document.removeEventListener("click", this.clickHandle);
  }
  listenRouter() {
    if (this.context.router) {
      this.context.router.listen(() => {
        const { visible } = this.state;
        visible && this.setState({ visible: false });
      });
    }
  }
  clickHandle = e => {
    if (
      !e.target.isEqualNode(this.target) &&
      $(this.refs.wea_popover_hrm_card).find(e.target).length === 0 &&
      this.instanceIsMounted
    ) {
      // 点击的不是触发节点 && 节点不在当前卡片上
      const { visible } = this.state;
      visible && this.setState({ visible: false });
    }
  };
  onLoad = e => {
    const bigImgWidth = e.target.naturalWidth || 142;
    const bigImgHeight = e.target.naturalHeight || 274;
    this.setState({ bigImgWidth, bigImgHeight });
  };
  onError = () => {
    const { userinfo } = this.state;
    userinfo.userimg = defaultIcon[userinfo.sex];
    this.setState({ userinfo });
  };
  getParentStyle = () => {
    const { children } = this.props;
    const { random } = this.state;
    const parent =
      this.refs.wea_popover_hrm ||
      document.getElementById(`wea_popover_hrm_${random}`);
    const com = children && parent ? $(parent) : "";
    com &&
      this.setState({
        pWidth: com.width(),
        pHeight: com.height(),
        pTop: com.offset().top,
        pLeft: com.offset().left
      });
  };
  renderCard() {
    const { children, inDialog = false } = this.props;
    const {
      userid,
      userinfo,
      showSQR,
      visible,
      x,
      y,
      targetHeight,
      loading,
      imgLoading,
      pTop,
      pLeft,
      winW,
      winH,
      validate,
      showBigImg,
      bigImgWidth,
      bigImgHeight,
      random
    } = this.state;
    const _pLeft = pLeft || 0;
    const _pTop = pTop || 0;
    const gender = {
      "Mr.": `${getLabel("28473", "男")}`,
      "Ms.": `${getLabel("28474", "女")}`
    };
    const sex = { "Mr.": "icon-coms-men", "Ms.": "icon-coms-women" };
    // Dialog dragable using translate, so position fixed & top + 50(title)
    const nowStyle = children
      ? {
          // display: visible ? 'block' : 'none',
          opacity: visible ? 1 : 0,
          position: inDialog ? "fixed" : "absolute",
          left: winW - x <= 450 ? winW - 450 : x - _pLeft,
          top:
            (winH - y <= 280 ? y - 280 - _pTop : y + targetHeight - _pTop) +
            (inDialog ? 50 : 0)
        }
      : {
          // display: visible ? 'block' : 'none',
          opacity: visible ? 1 : 0,
          position: "fixed",
          left: winW - x <= 450 ? winW - 450 : x,
          top: winH - y <= 280 ? y - 280 : y + targetHeight
        };
    nowStyle.transformOrigin = `${((x - _pLeft - nowStyle.left) / 424) *
      100}% ${y <= nowStyle.top + _pTop ? 0 : "100%"}`;
    const bigHeight = bigImgHeight > winH * 0.8 ? winH * 0.8 : bigImgHeight;
    const bigWidth = (bigHeight * bigImgWidth) / bigImgHeight;
    const bigImgStyle = showBigImg
      ? {
          width: bigWidth,
          height: bigHeight,
          left:
            nowStyle.left + bigWidth >= winW - (children ? _pLeft : 0)
              ? winW - (children ? _pLeft : 0) - bigWidth - nowStyle.left - 20
              : bigWidth > 142
              ? (bigWidth - 142) / 2 > nowStyle.left + _pLeft
                ? -(nowStyle.left + _pLeft - 20)
                : (142 - bigWidth) / 2
              : -2,
          top:
            nowStyle.top + bigHeight >= winH - (children ? _pTop : 0)
              ? winH - (children ? _pTop : 0) - bigHeight - nowStyle.top - 20
              : bigHeight > 214
              ? (214 - bigHeight) / 2
              : -2,
          opacity: 1,
          transformOrigin: "50% 50%",
          transition: "all .3s ease-in-out"
        }
      : {
          width: "100%",
          height: "100%",
          left: 0,
          top: 0,
          opacity: 0,
          transition: "all .2s ease-in-out"
        };
    const cls = classnames({
      "wea-popover-hrm-wrapper": true,
      "wea-popover-hrm-wrapper-show": visible
    });
    return (
      <div
        className="wea-popover-hrm-relative-parent"
        ref="wea_popover_hrm"
        id={`wea_popover_hrm_${random}`}
        onClick={e => {
          e.stopPropagation && e.stopPropagation();
        }}
      >
        {children || null}
        <div ref="wea_popover_hrm_card" className={cls} style={nowStyle}>
          <i
            title={"关闭"}
            className="icon-coms-Clear"
            onClick={() =>
              this.setState({
                visible: false,
                showBigImg: false,
                showSQR: false
              })
            }
          />
          <Spin spinning={loading}>
            {userinfo ? (
              <Row>
                <Col className="wea-popover-hrm-left">
                  <div className="wea-popover-hrm-left-img-wrap">
                    <img
                      className="wea-popover-hrm-userimg"
                      alt=""
                      src={
                        imgLoading
                          ? "/images/messageimages/temp/loading_wev8.gif"
                          : userinfo.userimg
                      }
                      onError={this.onError}
                      onLoad={this.onLoad}
                      onClick={() =>
                        !imgLoading && this.setState({ showBigImg: true })
                      }
                    />
                    <img
                      alt=""
                      className={`wea-popover-hrm-userimg ${
                        showBigImg ? "wea-popover-hrm-userimg-big" : ""
                      }`}
                      style={bigImgStyle}
                      src={userinfo.userimg}
                      onClick={() =>
                        !imgLoading &&
                        this.setState({ showBigImg: !showBigImg })
                      }
                      onError={this.onError}
                    />
                    <div
                      className="wea-popover-hrm-sqr"
                      style={{ display: showSQR ? "block" : "none" }}
                    >
                      <WeaQrcode
                        text={createQRCode(userinfo)}
                        size={105}
                        level={"L"}
                      />
                      <p>{getLabel("385903", "扫描添加联系人")}</p>
                    </div>
                    {validate[37] && validate[37].isValidate ? (
                      <div
                        className="wea-popover-hrm-left-corner"
                        title={
                          showSQR
                            ? getLabel("1290", "返回")
                            : validate[37].validateName
                        }
                        onClick={e => {
                          this.setState({ showSQR: !showSQR });
                          // 暂时未测出哪个管用。。
                          e.stopPropagation && e.stopPropagation();
                          e.preventDefault && e.preventDefault();
                          e.nativeEvent && e.nativeEvent.preventDefault();
                        }}
                      >
                        <i
                          className={`icon-coms-${
                            showSQR ? "Revoke2" : "Scan"
                          }`}
                        />
                      </div>
                    ) : null}
                  </div>
                  <div className="wea-popover-hrm-left-btns">
                    {validate.hasEmessage && validate.hasEmessage.isValidate ? (
                      <span className="wea-popover-hrm-left-btns-btn">
                        <i
                          className="icon-coms-Send-message"
                          title={validate.hasEmessage.validateName}
                          onClick={() => sendMsgToPC(userid, 0, "", "")}
                        />
                      </span>
                    ) : null}
                    {validate[31] && validate[31].isValidate ? (
                      <span className="wea-popover-hrm-left-btns-btn">
                        <i
                          className="icon-coms-message-o"
                          title={validate[31].validateName}
                          onClick={() =>
                            window.open(
                              `/spa/smallApp/static/index.html#/main/sms/SendSms?hrmid=${userid}`
                            )
                          }
                        />
                      </span>
                    ) : null}
                    {validate[19] && validate[19].isValidate ? (
                      <span className="wea-popover-hrm-left-btns-btn">
                        <i
                          className="icon-coms-Send-emails"
                          title={validate[19].validateName}
                          onClick={() =>
                            window.open(
                              `/spa/email/static/index.html#/main/email/new?isInternal=1&internaltonew=${JSON.stringify(
                                [{ id: userid, type: "resource" }]
                              )}`
                            )
                          }
                        />
                      </span>
                    ) : null}
                    {validate[32] && validate[32].isValidate ? (
                      <span className="wea-popover-hrm-left-btns-btn">
                        <i
                          className="icon-coms-New-schedule-o"
                          title={validate[32].validateName}
                          onClick={() =>
                            window.open(
                              `/spa/workplan/static/index.html#/main/wp/workPlanCreateSingle?type=create&selectedUser=${userid}`
                            )
                          }
                        />
                      </span>
                    ) : null}
                  </div>
                </Col>
                <Col className="wea-popover-hrm-right">
                  <div className="wea-popover-hrm-title">
                    <a
                      href={`/spa/hrm/index_mobx.html#/main/hrm/card/cardInfo/${userid}`}
                      title={userinfo.lastname}
                      target="_blank"
                      className="wea-f14"
                      style={{ color: "#2db7f5" }}
                    >
                      {userinfo.lastname}
                    </a>
                    {/*<span  title={`${sex[userinfo.sex]}${userinfo.workcode}`}>{`${sex[userinfo.sex]}${userinfo.workcode}`}</span>*/}
                    <em
                      title={`${gender[userinfo.sex]}`}
                      className={sex[userinfo.sex]}
                    />
                    <span title={userinfo.workcode}>{userinfo.workcode}</span>
                  </div>
                  <WeaNewScroll height={205}>
                    <div className="wea-popover-hrm-info">
                      <p>
                        <span title={getLabel("27511", "部门")}>
                          {getLabel("27511", "部门")}:
                        </span>
                        <a
                          href={`/spa/hrm/engine.html#/hrmengine/organization?showTree=false&isView=1&type=department&id=${
                            userinfo.departmentid
                          }`}
                          target="_blank"
                          // className='text-elli'
                          title={userinfo.departmentName}
                        >
                          {userinfo.departmentName}
                        </a>
                      </p>
                      <p>
                        <span title={getLabel("33553", "分部")}>
                          {getLabel("33553", "分部")}:
                        </span>
                        <a
                          href={`/spa/hrm/engine.html#/hrmengine/organization?showTree=false&isView=1&type=subcompany&id=${
                            userinfo.subcompanyid1
                          }`}
                          target="_blank"
                          title={userinfo.subcompanyName}
                        >
                          {userinfo.subcompanyName}
                        </a>
                      </p>
                      <p>
                        <span title={getLabel("6086", "岗位")}>
                          {getLabel("6086", "岗位")}:
                        </span>
                        <a
                          href={`/spa/hrm/engine.html#/hrmengine/posts?id=${
                            userinfo.jobtitle
                          }`}
                          target="_blank"
                          title={userinfo.jobtitleName}
                        >
                          {userinfo.jobtitleName}
                        </a>
                      </p>
                      <p>
                        <span title={getLabel("596", "上级")}>
                          {getLabel("596", "上级")}:
                        </span>
                        <a
                          href={`/spa/hrm/index_mobx.html#/main/hrm/card/cardInfo/${
                            userinfo.managerid
                          }`}
                          target="_blank"
                          title={userinfo.managerName}
                        >
                          {userinfo.managerName}
                        </a>
                      </p>
                      <p>
                        <span title={getLabel("23247", "状态")}>
                          {getLabel("23247", "状态")}:
                        </span>
                        <span title={userinfo.statusName}>
                          {userinfo.statusName}
                        </span>
                      </p>
                      <p>
                        <span title={getLabel("422", "手机")}>
                          {getLabel("422", "手机")}:
                        </span>
                        <span title={userinfo.mobile}>{userinfo.mobile}</span>
                      </p>
                      <p>
                        <span title={getLabel("421", "电话")}>
                          {getLabel("421", "电话")}:
                        </span>
                        <span title={userinfo.telephone}>
                          {userinfo.telephone}
                        </span>
                      </p>
                      <p>
                        <span title={getLabel("71", "邮件")}>
                          {getLabel("71", "邮件")}:
                        </span>
                        <span title={userinfo.email}>{userinfo.email}</span>
                      </p>
                    </div>
                  </WeaNewScroll>
                  <div className="wea-popover-hrm-bottom">
                    {userinfo.isOnline && userinfo.ip
                      ? [
                          <span className="wea-popover-hrm-info-online" />,
                          userinfo.ip
                        ]
                      : null}
                  </div>
                </Col>
              </Row>
            ) : (
              <div style={{ width: 428, height: 274 }} />
            )}
          </Spin>
        </div>
      </div>
    );
  }

  render() {
    const { children, inDialog = false } = this.props;
    if (children) {
      return this.renderCard();
    }
    return (
      <Extents
        className="wea-popover-hrm-inbody"
        container={WindowFunc.getTop().document.body}
        style={inDialog ? { position: "relative", zIndex: 1 } : {}}
      >
        {this.renderCard()}
      </Extents>
    );
  }
}

export default WeaPopoverHrm;



