import { Modal } from 'antd';
import WindowFunc from '../../wea-tools/window'

export const defaultIcon = {
  'Mr.': '/images/messageimages/temp/man_wev8.png',
  'Ms.': '/images/messageimages/temp/women_wev8.png',
};

export const utf16To8 = str => {
  const len = str.length;
  let out = '';
  for (let i = 0; i < len; i++) {
    const c = str.charCodeAt(i);
    if (c >= 0x0001 && c <= 0x007F) {
      out += str.charAt(i);
    } else if (c > 0x07FF) {
      out += String.fromCharCode(0xE0 | ((c >> 12) & 0x0F));
      out += String.fromCharCode(0x80 | ((c >> 6) & 0x3F));
      out += String.fromCharCode(0x80 | ((c >> 0) & 0x3F));
    } else {
      out += String.fromCharCode(0xC0 | ((c >> 6) & 0x1F));
      out += String.fromCharCode(0x80 | ((c >> 0) & 0x3F));
    }
  }
  return out;
};

export const createQRCode = userinfo => {
  const txt = `BEGIN:VCARD \nVERSION:3.0 \nN:${userinfo.lastname} \n` +
    `TEL;CELL;VOICE:${userinfo.mobile} \n` +
    `TEL;WORK;VOICE:${userinfo.telephone} \n` +
    `EMAIL:${userinfo.email} \n` +
    `TITLE:${userinfo.jobtitleName} \n` +
    `ROLE:${userinfo.departmentName} \n` +
    `ADR;WORK:${userinfo.locationName} \nEND:VCARD`;
  return utf16To8(txt);
};

/**
 * 调用e-message发起会话
 * @param conventioners
 * @param openType
 * @param groupName
 * @param extra
 */
export function sendMsgToPC(
  conventioners, openType, groupName, extra, successCallBack, errorCallBack
) {
  $.ajax({
    url: '/social/im/SocialIMOperation.jsp?operation=openPCconversation',
    data: {
      conventioners,
      openType,
      groupName,
      extra,
    },
    type: 'post',
    dataType: 'json',
    contentType: 'application/x-www-form-urlencoded; charset=utf-8',
    success(data) {
      if (data != null) {
        if (data.pcStatus == 0 && data.webStatus == 0) {
          // pc和web都不在
          Modal.warning({
            title: '系统提示',
            content: '检查到您的web端和客户端的e-message不在线，请打开任意一端进行会话！',
          });
        } else if (data.webStatus == 1) {
          // web在线
          if (WindowFunc.getTop().$('#socialIMFrm').length > 0) {

          } else {
            if (!!window.ActiveXObject || 'ActiveXObject' in window) {
              WindowFunc.getTop().Dialog.alert('您发起的e-message聊天已经建立，请到主页进行消息发送！');
            }
          }
        } else {
          // pc在线不作判断
        }
        typeof successCallBack === 'function' && successCallBack(data);
      } else {
        WindowFunc.getTop().Dialog.alert('对话创建失败！');
        typeof errorCallBack === 'function' && errorCallBack();
      }
    },
    error(XMLHttpRequest, textStatus, errorThrown) {
      WindowFunc.getTop().Dialog.alert('请求发送失败，请您检查当前网络！');
      typeof errorCallBack === 'function' && errorCallBack();
    },
  });
}



