import { Base64 } from "js-base64";
import isEmpty from "lodash/isEmpty";
import { message, Modal } from "antd";
window.E9Encrypt = false;

// fetch 部分处理
const server = window.server || "";
export const getFd = (values, deep) => {
  let fd = "";
  for (let p in values) {
    let target = values[p];
    if (typeof target === "function") {
      continue;
    }
    if (deep && typeof target === "object") {
      fd += getFd(target, deep);
    } else if (p == "jsonstr" && typeof target === "object") {
      let item = JSON.stringify(target);
      fd += p + "=" + item.replace(/\\/g, "") + "&";
    } else {
      // 处理undefined null数据to 空串
      target = target == undefined ? "" : target;
      let item = encodeURIComponent(target);
      fd += p + "=" + item + "&";
    }
  }
  return fd;
};

const getFetchParams = (method, params, deep) => {
  let obj = {
    method,
    headers: {
      "Content-Type": "application/x-www-form-urlencoded; charset=utf-8",
      "X-Requested-With": "XMLHttpRequest"
    }
  };
  if (server == "") {
    obj.credentials = "include";
  }
  if (!isEmpty(params) && method.toUpperCase() !== "GET") {
    obj.body = getFd(params, deep);
  }
  return obj;
};

const handleRejct = msg => {
  if (window.frameElement) {
    let url = window.frameElement.src;
    if (/.*emobile\/entrance.*/.test(url)) {
      return true;
    }
  }
  if (
    /.*\/wui\/index\.html.*#\/\?.*/.test(window.location.href) ||
    /.*\/wui\/index\.html$/.test(window.location.href)
  ) {
    return true;
  }
  if (!/.*emobile\/entrance.*/.test(window.location.href)) {
    message.error(msg);
    setTimeout(() => (window.location.href = "/wui/index.html"), 1000);
  }
  return true;
};

const checkReject = obj => {
  let isFalse = false;
  if (obj.errorCode && obj.errorCode === "001") {
    // session异常，如果有路由就强制跳转
    isFalse = handleRejct("session过期，请重新登陆！");
  } else if (obj.errorCode && obj.errorCode === "002") {
    // session异常，如果有路由就强制跳转
    isFalse = handleRejct("登陆超时，请重新登陆！");
  } else if (obj.errorCode && obj.errorCode === "003") {
    // session异常，如果有路由就强制跳转
    isFalse = handleRejct("您已被管理员强制下线！");
  } else if (obj.errorCode && obj.errorCode === "004") {
    // session异常，如果有路由就强制跳转
    isFalse = handleRejct("您已经从另一个窗口登入，当前工作窗口失效！");
  } else if (
    typeof obj.status !== "undefined" &&
    (!obj.status || obj.status === "false")
  ) {
    // message.error('接口业务逻辑错误：' + obj.error, 5);
    if (obj.error || obj.msg) {
      !/.*emobile\/entrance.*/.test(window.location.href) &&
        message.error(obj.error || obj.msg);
    }
    isFalse = true;
  }
  return isFalse;
};

export const callApi = (
  url,
  method = "GET",
  _params = {},
  type = "json",
  deep = false
) => {
  let params =
    Object.prototype.toString.call(_params) === "[object Object]"
      ? { ..._params }
      : {};
  if (Object.prototype.toString.call(_params) === "[object Array]") {
    params = [..._params];
  }
  if (
    typeof localStorage.access_token === "string" &&
    localStorage.access_token !== ""
  ) {
    params.access_token = localStorage.access_token;
  }
  url = `${server}${url}`;
  if (method.toUpperCase() === "GET" && !isEmpty(params)) {
    let c = "?";
    if (/\?/.test(url)) c = "&";
    url = `${url}${c}${getFd(params)}`;
  }
  if (method.toUpperCase() === "GET" && !/__random__/.test(url)) {
    let c = "?";
    if (/\?/.test(url)) c = "&";
    url += c + "__random__=" + new Date().getTime();
  }

  function evil(fn) {
    var Fn = window.Function; //一个变量指向Function，防止有些前端编译工具报错
    return new Fn("return " + fn)();
  }

  return new Promise((resolve, reject) => {
    let validateParams = JSON.stringify(_params);
    if (validateParams.indexOf("base64_") > -1) {
      console && console.error("不能提交数据：", _params);
      // regex
      const regexBase = /base64_(?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?/g;
      if (regexBase.test(validateParams)) {
        params = validateParams.replace(regexBase, oldStr => {
          return Base64.decode(oldStr.substr(7));
        });
        console && console.log("转换后的数据", JSON.parse(params));
      }
      if (params.indexOf("base64_") > -1) {
        reject("base64 不能提交:");
      } else {
        params = JSON.parse(params);
      }
    }
    fetch(url, getFetchParams(method, params, deep))
      .then(function(response) {
        return response.text().then(data => {
          if (response.redirected && /\/login\/Login.jsp/.test(response.url)) {
            if (
              /.*\/wui\/index\.html#\/?.*/.test(window.location.href) ||
              /.*\/wui\/index\.html$/.test(window.location.href)
            ) {
              return { status: true };
            }
            if (/.*emobile\/entrance.*/.test(window.location.href)) {
              return { status: false, errorCode: "002" };
            }
            if (window.frameElement) {
              let url = window.frameElement.src;
              if (/.*emobile\/entrance.*/.test(url)) {
                return { status: false, errorCode: "002" };
              }
            }
            message.error("登陆超时，请重新登陆！");
            setTimeout(() => (window.location.href = "/wui/index.html"), 1000);
          }
          if (type === "json") {
            try {
              return JSON.parse(data);
            } catch (error) {
              data = data || "{}";
              return evil("(" + data + ")"); // JSON.parse 解析反斜杠报错处理
            }
          } else {
            return data;
          }
        });
      })
      .then(data => {
        //weaHistory
        if (
          type === "json" &&
          data &&
          (data.status === "false" || !data.status) &&
          data.showType === "url" &&
          data.url
        ) {
          setTimeout(() => (window.location.href = data.url), 1000);
          resolve(data);
        } else if (
          type === "json" &&
          data &&
          (data.status === "false" || !data.status) &&
          data.showType === "auth"
        ) {
          if (window.weaHistory) {
            window.weaHistory.push({
              pathname: "/main/com/auth"
            });
          }

          resolve(data);
        } else if (
          type === "json" &&
          data &&
          (data.status === "false" || !data.status) &&
          (data.showMsg || data.showType === "msg")
        ) {
          data.error && message.warn(data.error);
          resolve(data);
        } else if (
          type === "json" &&
          data &&
          (data.status === "false" || !data.status) &&
          data.showType === "none"
        ) {
          resolve(data);
        } else if (
          type === "json" &&
          data &&
          (data.status === "false" || !data.status) &&
          (data.showModal || data.showType === "modal")
        ) {
          data.error &&
            Modal.warning({
              title: data.title || "消息提醒",
              content: data.error
            });
          resolve(data);
        } else if (type === "json" && checkReject(data)) {
          console &&
            console.error(`${url} fetch 请求异常: ${data.error || "error"}`);
          reject(`${url} fetch 请求异常: ${data.error || "error"}`, data);
        } else {
          resolve(data);
        }
      })
      .catch(function(ex) {
        console &&
          console.error(`${url} fetch 数据处理异常: ${ex}, params:`, params);
        reject(`${url} fetch 数据处理异常: ${ex}`);
      });
  });
};



