import WindowFunc from './window'

export const loadCss = () => {
  if (self != WindowFunc.getTop()) {
    if (window.notThrowCss !== true) {
      let topCss = [], currentCss = [], loadCss = [];
      $('link').each(function(){
        currentCss.push($(this).attr("href").slice(0, $(this).attr("href").indexOf('?')));
      })
      $(WindowFunc.getTop().document).find('link').each(function(){
        topCss.push($(this).attr("href").slice(0, $(this).attr("href").indexOf('?')));
      })
      currentCss.forEach((item)=> {
        if (topCss.indexOf(item) < 0) {
          loadCss.push(item);
        }
      })
      loadCss.forEach((item) => {
        WindowFunc.getTop().document.head.appendChild(
          $(`<link rel="stylesheet" type="text/css" href="${item}">`)[0])
      })
    }
  }
}


